@import "_theme.scss";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

//antd overrides

.site-layout .site-layout-background {
  background: #fff;

  h4 {
    margin: 0px 16px;
  }
}

.ant-layout-sider {
  background: $primary-black !important;
}

.ant-menu.ant-menu-dark,
.ant-menu-dark .ant-menu-sub,
.ant-menu.ant-menu-dark .ant-menu-sub {
  color: rgba(255, 255, 255, 0.65);
  background: $primary-black !important;
}

.ant-layout-sider-trigger {
  background: $primary-black-second !important;
}

.ant-menu-dark.ant-menu-dark:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background: $primary-black-second !important;
}

.ant-layout-footer {
  padding: 10px 50px !important;
  color: rgba(0, 0, 0, 0.85);
  font-size: 12px !important;
  background: #f0f2f5;
}

.ant-menu-submenu-open,
.ant-menu-submenu-active {
  color: #fff !important;
}

.ant-result-info .ant-result-icon>.anticon {
  color: $primary-black !important;
}

.ant-btn-primary:active {
  color: #fff;
  background: $primary-black-second !important;
  border-color: $primary-black !important;
}

.ant-btn-primary {
  color: #fff;
  background: $primary-black !important;
  border-color: $primary-black-second !important;
}

.ant-card-body {
  height: 350px !important;
}

.ant-table-content {
  overflow: auto;
}

.ant-input:focus, .ant-input-focused {
  border-color: $primary-black-second;
  border-right-width: 1px !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 2px $primary-black-shadow;
  box-shadow: 0 0 0 2px $primary-black-shadow;
}

.ant-input-number{
  width: 100%;
}

.ant-radio-button-wrapper:hover {
  position: relative;
  color: $primary-black-light;
}

.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled) {
  z-index: 1;
  color: $primary-black;
  background: #fff;
  border-color: $primary-black-light;
}

.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):active {
  color: $primary-black;
  border-color: $primary-black;
}

.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled)::before {
  background-color: $primary-black;
}

.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):hover {
  color: $primary-black-second;
  border-color: $primary-black-light;
}

.ant-input:hover, .ant-btn:hover, .ant-btn:active, .ant-btn:focus {
  border-color: $primary-black-second;
  border-right-width: 1px !important;
  color: $primary-black-second;
}

.ant-input-affix-wrapper:hover {
  border-color: $primary-black-second;
  border-right-width: 1px !important;
  z-index: 1;
}

.ant-upload.ant-upload-select-picture-card:hover {
  border-color: $primary-black-second;
}

@media only screen and (max-width: 1025px) {
  .ant-card-body {
    height: 250px !important;
  }
}